

.map {
  width: 320px;
  height: 200px;
}

@media (min-width: @tablet-width) {
  .map {
    width: 660px;
    height: 373px;
  }
}

@media (min-width: @desktop-width) {
  .map {
    width: 960px;
    height: 500px;
  }
}

.map__map-img {
  position: absolute;
  top: 40px;
  left: calc(50% - 160px);
  background-color: transparent;
  border: none;
  outline: none;
}

@media (min-width: @tablet-width) {
  .map__map-img {
    position: absolute;
    top: 30px;
    left: calc(50% - 330px);
    background-color: transparent;
    border: none;
    outline: none;
  }
}

@media (min-width: @desktop-width) {
  .map__map-img {
    position: absolute;
    top: 23px;
    left: calc(50% - 480px);
    background-color: transparent;
    border: none;
    outline: none;
  }
}
