

.contact-title {
  width: 270px;
  text-align: center;
  padding-left: 32px;
  padding-right: 32px;
}

.contact-title {
  font-size: 0px;
  line-height: 0px;
}

@media (min-width: @tablet-width) {
  .contact-title {
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    line-height: 30px;
    font-weight: 400;
    text-transform: uppercase;
    margin: 0;
    color: #d22856;
    text-align: center;

  }
}

