.main-nav__switch {
  position: absolute;
  font-size: 0;
  top: 21px;
  right: calc(50% - 140px);
  background-image: url("../img/icon-menu-burger.svg");
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  margin: 0;
}
.main-nav__switch--hide {
  background-image: url("../img/icon-menu-burger.svg");
  background-color: transparent;
  background-repeat: no-repeat;
  width: 50px;
  height: 24px;
  border: none;
}
@media (min-width: 660px) {
  .main-nav__switch--hide {
    position: absolute;
    top: 43px;
    right: calc(50% - 310px);
    background-image: url("../img/icon-menu-burger.svg");
    background-color: transparent;
    background-repeat: no-repeat;
    width: 50px;
    height: 24px;
    border: none;
    cursor: pointer;
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .main-nav__switch--hide {
    display: none;
  }
}
.main-nav__switch--hide:active ~ .site-list--open {
  display: block;
}
.main-nav__switch--hide:active ~ .main-nav {
  position: absolute;
  top: 367px;
}
.main-nav__switch--close {
  display: block;
  background-image: url("../img/icon-menu-cross.svg");
  background-color: transparent;
  background-repeat: no-repeat;
  width: 25px;
  height: 25px;
  border: none;
}
